<template>
    <el-container class="container">
        <el-aside width="240px">
            <!-- 左侧的当行 -->
            <Aside></Aside>
        </el-aside>
        <el-container>
            <el-header height="70px">
                <Header></Header>
            </el-header>
            <el-main>
                <router-view></router-view>
            </el-main>
        </el-container>
    </el-container>
</template>
<script>
import Aside from './aside/aside'
import Header from './header/header'
import LaravelEcho from '@/utils/laravel-echo'

// 订阅扫码登录事件

export default{
    data(){
        return{

        }
    },
    created(){
        this.initWs();
    },
    components:{
       Aside,
       Header
    },
    methods:{
        initWs(){
            LaravelEcho.instance.channel('SysInfo').listen('SysInfoEvent', (e) => {
                console.log(e)
            })
        }
    }
}
</script>
<style scoped lang="scss">
    .container{
        height: 100vh;
    }
</style>



