<!--
 * @Descripttion: 设备列表
 * @version: 1.0
 * @Author: GaoX
 * @Date: 2022-04-21 12:18:09
 * @LastEditors: Please set LastEditors
 * @LastEditTime: 2022-05-10 20:14:35
-->
<template>
    <div class="services-list">
        <div class="service-item">
            <div class="service-title">
                <p class="service-border"></p>
                <p class="service-tit">服务器</p>
            </div>
            <div class="service-content">
                <router-link v-for="(item,index) in list" :key="item.id" tag="div" class="service-info" to="/index/servicesinfo">
                    <div class="service-icon">
                        <i class="el-icon-office-building"></i>
                    </div>
                    <div class="service-name">
                        <p class="service-name-title">{{item.name}}</p>
                        <div class="service-name-info">
                            <p>{{item.host}}</p>
                            <p>运行中</p>
                        </div>
                    </div>
                </router-link>
            </div>
        </div>
    </div>
</template>
<script>
    export default{
        data(){
            return{
               list:null
            }
        },
        methods:{
           getEnableServices(){
               var params = {}
                capis.getEnableServices(params,{})
                .then(data=>{
                    this.tableLoading = false;
                    if(data.status == 'success'){
                        //对数据进行赋值
                        this.list = data.res.data;
                    }else{
                        this.$notify({
                            title: '提示',
                            message: data.des,
                            type: 'warning'
                        });
                    }
                
                })
                .catch(err=>{
                    this.tableLoading = false;
                    this.$notify({
                        title: '提示',
                        message: '操作失败',
                        type: 'warning'
                    });
                })
           }
        },
        created(){
            this.getEnableServices();
        },
        components:{
        },
        computed:{
            
        }
    }
</script>

<style scoped lang="scss">
    .services-list{
        box-sizing: border-box;
        background-color: #fff;
        box-shadow: 0px 0px 25px 0px rgba(0,0,0,0.1);
        border-radius: 20px;
        padding: 40px 80px;
        .service-item{
            .service-title{
                display: flex;
                
                font-size: 20px;
                font-weight: 600;
                .service-border{
                    width: 10px;
                    height: 26px;
                    background-color: #5e5ec6;
                    border-radius: 5px;
                }
                .service-tit{
                    margin-left: 20px;
                }
            }
            .service-content{
                padding: 20px 0;
                display: flex;
                flex-direction: row;
                flex-wrap: wrap;
                .service-info{
                    cursor: pointer;
                    box-sizing: border-box;
                    height: 94px;
                    width: 345px;
                    border: 2px solid #cccccc;
                    border-radius: 10px;
                    padding: 18px 24px;
                    display: flex;
                    margin: 0 20px 20px 0;
                    .service-icon{
                        font-size: 44px;
                    }
                    .service-name{
                        margin-left: 28px;
                        flex: 1;
                        .service-name-title{
                            font-size: 20px;
                            font-weight: 500;
                        }
                        .service-name-info{
                            display: flex;
                            flex-direction: row;
                            justify-content: space-between;
                            width: 100%;
                            margin-top: 5px;
                            font-size: 14px;
                            color: #666;
                        }
                    }
                }
                .service-info:hover{
                    border: 2px solid #5e5ec6;
                }
            }
        }
    }
</style>


