 * @LastEditors: Please set LastEditors
 * @LastEditTime: 2022-04-25 16:09:45
-->
<template>
   <div class="top">
       <div class="left">
           <router-link to="/index/welcom" class="index-iocn" tag="p">
                <span class="icon" :class="[activeIconClass]"><i class="el-icon-s-home"></i></span>
                <span class="title">首页</span>
           </router-link>
           <!-- <p class="other-menu">
               <span class="title">设备管理</span>
           </p> -->
       </div>
       <div class="right">
           <div class="notice" @click="routeToMessage" :class="[activeMessageClass]">
               <el-tooltip class="item" effect="light" content="报错与告警" placement="bottom">
                <el-badge is-dot class="item">
                        <i class="el-icon-bell"></i>
                </el-badge>
               </el-tooltip>
           </div>
           <div class="time">
               <!-- 时间 -->
               {{time.hours}}<span :style="{visibility:time.minutesflag?'visible':'hidden'}">:</span>{{time.minutes}}
           </div>
            <div class="date">
                <!-- 年月日 -->
                <p>{{time.yyyMMdd}}</p>
                <!-- 星期 -->
                <p>{{time.week}}</p>
            </div>
       </div>
   </div>
</template>
<script>
    export default{
        data(){
            return{
                activeIconClass:'',
                activeMessageClass:'',
                getTimeintervalObj:null, //时间定时器的对象
                time:{
                    yyyMMdd:'', //日期 年月日
                    week:'', //周
                    hours:'', //小时
                    minutes:'', //分数
                    minutesflag:true
                }
            }
        },
        created(){
            // 执行计时器，显示时间
            this.getTimes();
        },
        methods:{
            routeToMessage(){
                this.$router.push({path:'/index/message'})
            },
            getRoutePath(to,from){
                if(this.$route.path == '/index/welcom')
                {
                    this.activeIconClass='active';
                }
                else
                {
                    this.activeIconClass='';
                }
                 if(this.$route.path == '/index/message')
                {
                    this.activeMessageClass='active';
                }
                else
                {
                    this.activeMessageClass='';
                }
            },
            getTimes(){
                // console.log(82)
                this.getTimeintervalObj=setTimeout(()=>{
                    // 定时器 一秒执行一次
                    var myDate = new Date();
                    if((parseInt(myDate.getMonth())+1)<10)
                    {
                        var months = '0'+(parseInt(myDate.getMonth())+1);
                    }
                    else
                    {
                        var months =  parseInt(myDate.getMonth())+1;
                    }
                    this.time.yyyMMdd = myDate.getFullYear()+'-'+months+'-'+myDate.getDate(); //年月日
                    var weeksDate=['星期日','星期一','星期二','星期三','星期四','星期五','星期六']
                    this.time.week= weeksDate[myDate.getDay()]; //周
                    this.time.hours = myDate.getHours();//小时
                    this.time.minutes = myDate.getMinutes();//分钟
                    if(this.time.minutes<10)
                    {
                        this.time.minutes = '0'+this.time.minutes;
                    }
                    //判断是否显示两个点
                    this.time.minutesflag=!this.time.minutesflag;
                    this.getTimes();
                },1000);
                

            }
        },
        watch:{
            '$route':'getRoutePath'
        }
    }
</script>
<style scoped lang="scss">
   .top{
       height: 100%;
       display: flex;
       justify-content: space-between;
       .left{
           height: 100%;
           color: #5e5ec6;
           display: flex;
           align-items: center;
           .index-iocn{
                cursor: pointer;
                .icon 
                {
                    background-color: #fff;
                    display: inline-block;
                    width: 32px;
                    height: 32px;
                    text-align: center;
                    border-radius: 5px;
                    box-shadow: 0 0 15px 0 rgba(0,0,0,0.1);
                    i{
                        color: #5e5ec6;
                        line-height: 32px;
                    }
                }
                //激活状态
                .icon.active{
                    background-color: #5e5ec6;
                    i{
                        color: #fff;
                    }
                }
                .title
                {
                    margin-left: 10px;
                }
           }
           .other-menu
           {
                height: 32px;
                cursor: pointer;
                margin-left: 20px;
                line-height: 30px;
           }
          
       }
       .right{
           display: flex;
           align-items: center;
           color: #5d73c7;
           .notice{
               width: 32px;
               height: 32px;
               background-color: #fff;
               text-align: center;
               border-radius: 5px;
               box-shadow: 0 0 15px 0 rgba(0,0,0,0.1);
               cursor: pointer;
               i{
                   font-size: 22px;
                   line-height: 32px;
                   color: #5d73c7;
               }
           }
           .notice.active{
               background-color: #5d73c7;
               i{
                   color: #fff;
               }
           }
           .time{
               margin-left: 35px;
               font-size: 22px;
               margin-right: 10px;
           }
           .date{
               border-left: 3px solid #5d73c7;
               border-radius: 2px;
               padding-left: 10px;
               font-size: 16px;
           }
       }
   }
</style>


