            <p class="message-title">告警与报错</p>
            <div class="message-type">
                 <el-select v-model="type" placeholder="请选择" clearable>
                    <el-option
                    v-for="item in warning_type"
                    :key="item.value"
                    :label="item.title"
                    :value="item.value">
                        <span style="display:inline-block;width:12px;height:12px; border-radius:50%;" :style="[{backgroundColor:item.color}]"></span>
                        <span style="color: #8492a6; font-size: 13px">{{ item.title }}</span>
                    </el-option>
                </el-select>
            </div>
        </div>
        <div class="message-table">
            <el-table
            ref="record-table"
            :data="messageJson"
            style="width: 100%"
            stripe
            :height="tableHeight"
            highlight-current-row
            >
                <el-table-column
                label="详情"
                sortable
                >
                    <template slot-scope="scope">
                        <span style="display:inline-block;width:12px;height:12px; border-radius:50%;" :style="[{backgroundColor:scope.row.color}]"></span>
                        <span style="margin-left: 10px">{{ scope.row.content }}</span>
                    </template>
                </el-table-column>
                <el-table-column
                label="设别名称或者ip"
                width="150"
                sortable
                >
                 <template slot-scope="scope">
                    <p>{{ scope.row.name }}</p>
                    <p>{{ scope.row.ip }}</p>
                </template>
                </el-table-column>
                <el-table-column
                prop="date"
                label="时间"
                width="200"
                sortable
                fixed="right"
                >
                </el-table-column>
            </el-table>
        </div>
        <div class="message-pagenation" ref="message-pagenation">
            <el-pagination
