<template>
    <div class="aside_content">
        <div class="login_tiltle">
            <div>
                <img src="../../../assets/index/logo.png" alt="">
            </div>
            <div>
                <span>服务检测系统</span>
            </div>
        </div>
        <div class="menu">
             <el-menu 
                class="el-menu-vertical-demo"
                background-color="#5e5ec6"
                text-color="#fff"
                active-text-color="#e9cd5e"
                :router="true"
             >
                <el-menu-item index="/index/devices">
                    <i class="el-icon-s-operation"></i>
                    <span slot="title">设备管理</span>
                </el-menu-item>
                <el-submenu index='1'>
                    <template slot="title">
                        <i class="el-icon-warning-outline"></i>
                        <span>告警设置</span>
                    </template>
                    <el-menu-item-group>
                        <el-menu-item index="/index/warning">告警策略</el-menu-item>
                        <el-menu-item index="/index/warninglog">告警日志</el-menu-item>
                    </el-menu-item-group>
                </el-submenu>
                <el-menu-item index="/index/services">
                    <i class="el-icon-files"></i>
                    <span slot="title">设备列表</span>
                </el-menu-item>
                <el-submenu  index='2'>
                    <template slot="title">
                        <i class="el-icon-setting"></i>
                        <span>常用设置</span>
                    </template>
                    <el-menu-item-group>
                        <el-menu-item index="/index/custom">人员管理</el-menu-item>
                        <el-menu-item index="/index/word">字冠管理</el-menu-item>
                    </el-menu-item-group>
                </el-submenu>
                 <el-menu-item index="/index/firewall">
                    <i class="el-icon-help"></i>
                    <span slot="title">防火墙</span>
                </el-menu-item>
                 <el-submenu  index='3'>
                    <template slot="title">
                        <i class="el-icon-set-up"></i>
                        <span>高级设置</span>
                    </template>
                    <el-menu-item-group>
                        <el-menu-item index="/index/params">系统参数</el-menu-item>
                    </el-menu-item-group>
                </el-submenu>
            </el-menu>
        </div>
        <div class="login_out" @click="loginOut">
            <p title="退出登录">
                <img src="../../../assets/index/login_out.png" alt="">
                <span>退出登录</span>
            </p>
        </div>
    </div>
</template>
<script>

export default{
    data(){
        return{

        }
    },
    methods:{
        loginOut(){
             this.$confirm('确定要退出？', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'error'
                }).then(() => {
                    var loading = this.$loading({
                lock: true,
                text: 'Loading',
                spinner: 'el-icon-loading',
                background: 'rgba(0, 0, 0, 0.7)'
              })
              var params = {}
              capis.logout(params,{})
              .then(data=>{
                loading.close();
                if(data.status == 'success'){
                  ctool.gotLogin({
                    title: '退出成功',
                    message: '感谢使用！',
                    type: 'success'
                  });
                }else{
                  this.$notify({
                    title: '登录失败',
                    message: '账号或密码不正确',
                    type: 'warning'
                  });
                }
                
              })
              .catch(err=>{
                loading.close();
                this.$notify({
                  title: '提示',
                  message: '操作失败',
                  type: 'warning'
                });
              })
            });
            
        }
    }
}
</script>
<style scoped lang="scss">
    .aside_content{
        background-color: #5e5ec6;
        height: 100%;
        border-radius: 0 50px 0 0;
        .login_tiltle{
            height: 120px;
            display: flex;
            justify-content: center;
            align-items: center;
            span{
                color: #ffffff;
                font-size: 24px;
                margin-left: 8px;
            }
        }
        .el-menu{
            border: none;
            i{
                color:#fff;
            }
            .el-menu-item.is-active{
                i{
                    color: #e9cd5e;
                }
               
            }
        }
        .login_out{
            position: absolute;
            bottom: 40px;
            left:55px;
            p{
                cursor: pointer;
                img{
                    width: 18px;
                    height: 18px;
                    vertical-align: middle;
                }
                span{
                    margin-left: 16px;
                    font-size: 16px;
                    line-height: 26px;
                    color: #ffffff;
                    
                }
            }
        }
    }
</style>


