<template>
    <div class="serviceinfo-content">
        <div class="serviceinfo-title">
            <p class="serviceinfo-border"></p>
            <p class="serviceinfo-tit">硬盘存储</p>
        </div>
        <div class="disk-info">
            <div class="disk-item-info">
                <p class="disk-item-title">文件系统:/dev/vda1 类型:ext4 </p>
                <el-progress :text-inside="true" :stroke-width="20" :percentage="50" status="success"></el-progress>
            </div>
