            </div>
        </div>
        <el-divider></el-divider>
    </div>
    
</template>
<script>
 export default{
    
    data(){
        return{
             echarts:null,
        }
    },
    components:{
    },
    mounted(){
    },
}
</script>
<style scoped lang="scss">
    .serviceinfo-content{
        .serviceinfo-title{
            display: flex;
            font-size: 20px;
            font-weight: 600;
            margin-bottom: 20px;
            .serviceinfo-border{
                width: 10px;
                height: 26px;
                background-color: #5e5ec6;
                border-radius: 5px;
                margin-left: 20px;
            }
            .serviceinfo-tit{
                margin-left: 18px;
            }
        } 
    }
    .disk-item-title{
        font-size: 18px;
        color: #999;
        height: 37px;
        line-height: 37px;
    }
</style>



