<template>
    <div class="servicesinfo-list">
        <div class="top-tilte">
             <div class="serviceinfo-title">
                <p class="serviceinfo-back"><i class="el-icon-back"></i></p>
                <p class="serviceinfo-border"></p>
                <p class="serviceinfo-tit">服务器</p>
            </div>
            <div class="service-name">
                <p class="sevice-name-info">CTI服务器</p>
                <p class="sercvice-device-type">
                    <span class="type">centos</span>
                    <span class="servce-ip">IP:192.168.31.247</span>
                </p>
            </div>
        </div>
        <el-divider></el-divider>
        <Status></Status>
         
        <Disk></Disk>
           
        <Liuliang></Liuliang>
        
        <Manage></Manage>
    </div>
</template>
<script>
    import Disk from './disk.vue'
    import Status from './status.vue'
    import Liuliang from './liuliang.vue'
    import Manage from './mange.vue'
    export default{
        data(){
            return{
               
            }
        },
        methods:{
           
        },
        components:{
            Disk
            ,Status
            ,Liuliang
            ,Manage
        },
        mounted(){
        },
    }
</script>

<style scoped lang="scss">
    .servicesinfo-list{
        box-sizing: border-box;
        background-color: #fff;
        box-shadow: 0px 0px 25px 0px rgba(0,0,0,0.1);
        border-radius: 20px;
        padding: 40px 80px;
        .top-tilte{
            .serviceinfo-title{
                display: flex;
                font-size: 20px;
                font-weight: 600;
                .serviceinfo-back{
                    i{
                        font-size: 30px;
                        color: #ceceee;
                    }
                }
                .serviceinfo-border{
                    width: 10px;
                    height: 26px;
                    background-color: #5e5ec6;
                    border-radius: 5px;
                    margin-left: 20px;
                }
                .serviceinfo-tit{
                    margin-left: 20px;
                }
            }
            .service-name{
                .sevice-name-info{
                    font-size: 22px;
                    font-weight: 600;
                    margin-top: 20px;
                }
                .sercvice-device-type{
                    font-size: 14px;
                    color: #999999;
                    margin-top: 20px;
                }
            }

        }
        .serviceinfo-content{
           .serviceinfo-title{
                display: flex;
                font-size: 20px;
                font-weight: 600;
                margin-bottom: 20px;
                .serviceinfo-border{
                    width: 10px;
                    height: 26px;
                    background-color: #5e5ec6;
                    border-radius: 5px;
                    margin-left: 20px;
                }
                .serviceinfo-tit{
                    margin-left: 18px;
                }
            } 
        }
       
    }
</style>


