<template>
    <div class="line-info">
        <div class="radial">
            <Radial
            size="100"
            color="#5e5ec6"
            backgroundColor="#d4d4d4"
            percentNum="53"
            topmenu="CPU使用率"
            bottommenu="2核"
            ></Radial>
        </div>
        <div class="line">
            <!-- 折线图 -->
            <div class="" :id="elid" :style="{ height: '360px','margin-bottom':'20px'}"></div>
        </div>
        
    </div>
</template>
<script>
import Radial from '@/views/radial/radial'
 export default{
     props:{
         elid:{
             type:String
         }
     },
    data(){
        return{
             echarts:null,
        }
    },
    methods:{
         initEchats(){
                var option = {
                    xAxis: {
                        type: 'category',
                        data: ['3-21', '3-22', '3-23', '3-24', '3-25', '3-26', '3-27'],
                        axisPointer: {
                            lineStyle: {
                                color: '#5e5ec6', //提示线颜色
                                width: 2
                            },
                        },
                    },
                    yAxis: {
                        type: 'value'
                    },
                    tooltip: {
                        trigger: 'axis'
                    },
                    toolbox: {
                        feature: {
                        saveAsImage: {}
                        }
                    },
                    grid:{
                        x:50, //左侧未知
                        // y:0
                        x2:0 //右侧
                    },

                    series: [
                        {
                        data: [0, 932, 901, 934, 1290, 1330, 0],
                        type: 'line',
                        smooth: true,
                        itemStyle:{
                            color:'#5e5ec6' //线的颜色
                        },
                        areaStyle:{
                            normal:{
                                color:new this.$echarts.graphic.LinearGradient(0, 0, 0, 1, [
                                    {
                                        offset: 0,
                                        color: 'rgba(93,115,199,0.5)' //渐变上
                                    },
                                    {
                                        offset: 1,
                                        color: 'rgba(93,115,199,0)' //渐变下
                                    }
                                ]
                            )}
                            }
                        }
                    ]
                };
                this.echarts = this.$echarts.init(document.getElementById(this.elid));
                this.echarts.setOption(option);
            }
    },
    components:{
       Radial 
    },
    mounted(){
        this.initEchats();
    },
}
</script>
<style scoped lang="scss">
    .line-info{
        display: flex;
        flex-direction: row;
        .radial{
            width: 100px;
        }
        .line{
            flex: 1;
            margin-left: 40px;
        }
    }
    
</style>



