import {post,postObj,postBlob,downloadPost,get} from '@/axios/index'

const apis = { 
  login: (params,headers) => post('/api/auth/login',params,headers),// 登录接口
  logout: (params,headers) => post('/api/auth/logout',params,headers),// 登出接口
  devicesList: (params,headers) => post('/api/host/list',params,headers),// 登出接口
  getRoleList: (params,headers) => post('/api/host/role/list',params,headers),// 获取到服务器类型
  devicesSave: (params,headers) => post('/api/host/save',params,headers),// 服务器的添加和编辑
  getDevicesInfo: (params,headers) => post('/api/host/info',params,headers),// 获取到服务器信息
  devicesDel: (params,headers) => post('/api/host/del',params,headers),// 服务器的删除
  getEnableServices: (params,headers) => post('/api/host/list/enable',params,headers),// 获取到启用的服务器数据
  getContactList: (params,headers) => post('/api/contact/list',params,headers),// 获取用户列表
  contactSave: (params,headers) => post('/api/contact/save',params,headers),// 修改用户状态
  deleteCustom: (params,headers) => post('/api/contact/del',params,headers),// 删除用户状态
  getContactInfo: (params,headers) => post('/api/contact/info',params,headers),// 获取到用户信息
  getContactListEnable: (params,headers) => post('/api/contact/list/enable',params,headers),// 获取到启用的用户信息
  getSettingList: (params,headers) => post('/api/web/setting/list',params,headers),// 获取到系统参数
  paramsSave: (params,headers) => post('/api/web/setting/save',params,headers),// 系统参数装填修改
  getNoticeList: (params,headers) => post('/api/notification/setting/list',params,headers),// 告警规则列表
  deleteWarning: (params,headers) => post('/api/notification/setting/del',params,headers),// 告警规则删除
  getNoticeSave: (params,headers) => post('/api/notification/setting/save',params,headers),// 告警规则添加
  warningStatusEdit: (params,headers) => post('/api/notification/setting/save',params,headers),// 告警规则添加
  getNotificationInfo: (params,headers) => post('/api/notification/setting/info',params,headers),// 告警规则详情
  getNoticeLogList: (params,headers) => post('/api/notification/log/list',params,headers),// 告警记录
  getNotificationSettingListEnable: (params,headers) => post('api/notification/setting/list/enable',params,headers),// 获取到启用的告警规则
}
export default apis




